import status from '../services/status'

export default {

	data () {
		return {
			statusText:"机房",
			flag:false,
			back:'后退'
		}
	},

	init () {

	},

	ready () {
		status.setThis.call(status, this);
	},

	methods : {
		backUp () {
			status.backUp();
		}
	},

	initBus (bus) {
	    status.regBus(bus);
	},

	components : {}
}


